.app-wrapper {
  margin: 0 auto;
  display: grid;
  width: 1200px;
  grid-template-areas: "h h" "n c";

  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  font-family: 'Montserrat', sans-serif;
}

.app-wrapper-content {
  grid-area: c;
}
html {
  background-color: #edeef0;
}
